<template>
  <div>
    <v-card elevation="2" outlined>
      <v-card-text>
        <p class="text-h6 text--primary">
          Fault Definitions
        </p>
        <v-container>
          <v-row v-for="(row, i) in RowData" :key="i">
            <v-col v-for="(item, index) in row.Column" :key="index">
              <v-row>
                <v-col cols="4" align-self="center" class="text-body-1 d-flex justify-end font-italic"> {{item.name}} </v-col>
                <v-col align-self="center" class="text-body-1 d-flex justify-start font-weight-bold red--text"> 
                  <v-btn-toggle mandatory color="primary">
                    <div v-for="(fault, j) in item.faultOptions" :key="j">
                      <v-btn outlined :value="fault" :disabled="fault != item.faultType" class="primary--text">
                        {{fault}}
                      </v-btn>
                    </div>
                  </v-btn-toggle>
                </v-col>
              </v-row>
            </v-col>
          </v-row>
        </v-container>

      </v-card-text>
    </v-card>

  </div>  
</template>


<script>
import { mapActions } from 'vuex';

const AllRows = [
  {
    Column: [
    {
      name: "Overdriven",
      faultType: "Fault",
      faultOptions: ["Fault", "Latched", "None"]
    },
    {
      name: "Reverse Power",
      faultType: "Fault",
      faultOptions: ["Fault", "Latched", "None"]
    }],
  },

  {
    Column: [
    {
      name: "Temperature Fault",
      faultType: "Fault",
      faultOptions: ["Fault", "Latched", "None"]
    },
    {
      name: "Inhibit",
      faultType: "Closed",
      faultOptions: ["Closed", "Open"]
    }]
  }
];

// const PowerConversionOptions = ["dBm", "dBW", "W"];

export default {
  name: 'RfSettings',
  
  props: {
    BootupRf: {
      type: Boolean, 
      default: false
    },

    RfOffset: {
      type: Number,
      default: 3.7
    },

    PowerConversion: {
      type: Number,
      default: 0,
    },
  },

  created() {
    this.RowData = AllRows;
    // this.PowerConversionOptions = PowerConversionOptions;
  },

  computed: {
    IsDifferent() {
      return this.BootupRf != this.new_BootupRf
        || this.RfOffset != this.new_RfOffset
        || this.PowerConversion != this.new_PowerConversion;
    },

    OffsetFormat() {
      var formatted = parseFloat(this.RfOffset).toFixed(2);
      console.log(formatted);
      return formatted;
    }
  },

  methods: {
    ...mapActions(["PerformSetRfSettings"]),

    ChangeRfSettings() {
      var rfSettings = {
        BootupRf: this.new_BootupRf,
        RfOffset: Number.parseFloat(this.new_RfOffset),
        PowerConversion: this.new_PowerConversion
      };

      this.PerformSetRfSettings(rfSettings);
    },

  },

  watch: {
    new_PowerConversion: function() {
      console.log(this.new_PowerConversion);
    }
  },

  data: function() {
    return {
      new_RfOffset: this.RfOffset,
      new_BootupRf: this.BootupRf,
      new_PowerConversion: this.PowerConversion
    }
  },


}
</script>

<style scoped>
.input-field {
  padding: 5px;
  margin: 5px;
}
</style>