<template>
  <v-card elevation="2" outlined>
    <v-card-text>
      <v-row class="pa-md-4">
        <p class='text-h6 text--primary'>
          Settings 
        </p>
        <v-spacer></v-spacer>
        
        <v-dialog v-model="rebootDialog" width="600px">
          <template v-slot:activator="{on, attrs}">
            <v-btn class="mx-1" v-bind="attrs" v-on="on"> 
              Reboot Unit
            </v-btn>
          </template>

          <v-card>
            <v-card-title>
              Rebooting Unit
            </v-card-title>

            <v-card-text>
              <p class='text-body-1 d-flex align-center'>
                Do you want to reboot the unit?
              </p>

              <v-btn class="mx-1" v-on:click="ConfirmRebootUnit(true)"> Reboot </v-btn>
              <v-btn class="mx-1" v-on:click="ConfirmRebootUnit(false)"> Cancel </v-btn>
            </v-card-text>

          </v-card>

        </v-dialog>
      </v-row>

      <v-container>
        <v-row>
          <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Model</v-col>
          <v-col cols="3" class='text-body-1 d-flex align-center font-weight-bold'> {{Info.Model}} </v-col>

          <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Firmware</v-col>
          <v-col cols="3" class='text-body-1 d-flex align-center font-weight-bold'> {{Info.Firmware}} </v-col>
        </v-row>

        <v-row>
          <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Serial</v-col>
          <v-col cols="3" class='text-body-1 d-flex align-center font-weight-bold'> {{Info.Serial}} </v-col>
          
          <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Bootloader</v-col>
          <v-col cols="3" class='text-body-1 d-flex align-center font-weight-bold'> {{Info.Bootloader}} </v-col>
        </v-row>

        <v-row>
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Attenuator</v-col>          
          <v-col align-self="center" cols="2" class="text-body-1 font-weight-medium">
            <v-text-field type="number" suffix="dB" dense hide-details
              :step="UnitOption.Tx.StepAtten.Rez" :min="UnitOption.Tx.StepAtten.Min" :max="UnitOption.Tx.StepAtten.Max"
              :rules="[StepAttenIsValid(new_StepAtten)]"
              :label="StepAttenIncrementString" 
              v-model.number="new_StepAtten"> </v-text-field> 
          </v-col>
          <v-col cols="1"> </v-col>
        
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Software Mute</v-col>
          <v-col align-self="center" cols="3">
            <v-btn-toggle color="primary" mandatory v-model="new_SwEnable">
              <v-btn outlined :value="true"> 
                Unmute
                <v-icon dark right> mdi-checkbox-marked-circle </v-icon>
              </v-btn>
              <v-btn outlined :value="false">
                Mute 
                <v-icon dark right> mdi-cancel </v-icon>
              </v-btn>
            </v-btn-toggle>
          </v-col>
        </v-row>

        <v-row>
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'> Alias </v-col>
          <v-col align-self="center" cols="3" class="text-body-1 font-weight-medium">
            <v-text-field v-model="new_Alias" v-debounce:1s="SetNewAlias" dense hide-details></v-text-field>
          </v-col>

          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> RF Offset </v-col>
          <v-col cols="2" align-self="center" class="text-body-1 font-weight-medium">
            <v-text-field suffix="dB" dense hide-details
              v-model.number="new_RfOffset"
              type="number" label="RF Offset"
              step="0.10"
              > </v-text-field>
          </v-col>
          <v-col cols="1"></v-col>
        </v-row>

        <v-row>
          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> Power-On RF State </v-col>
          <v-col cols="2" align-self="center" >
            <v-btn-toggle color="primary" mandatory v-model="new_BootupRf">
              <v-btn outlined :value="true">
                Enabled
              </v-btn>
              <v-btn outlined :value="false">
                Disabled
              </v-btn>
            </v-btn-toggle>
          </v-col>
          <v-col cols="1"></v-col>

          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> Power Conversion </v-col>
          <v-col cols="2" align-self="center" class="text-body-1 font-weight-medium">
            <v-btn-toggle mandatory color="primary" v-model="new_PowerConversion">
              <div v-for="(type, i) in PowerConversionOptions" :key="i">
                <v-btn outlined :value="i" class="primary--text">
                  {{type}}
                </v-btn>
              </div>
            </v-btn-toggle>
          </v-col>
        </v-row>
      </v-container>
      
    </v-card-text>

    <v-card-text>
      <p class='text-h6 text--primary'> ALC </p>
      <v-container>
        <v-row>
          <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>ALC Setpoint </v-col>
          <v-col cols="2" align-self="center" class="text-body-1 font-weight-medium">
            <v-text-field suffix="dBm" dense hide-details
              v-model.number="new_setpoint"
              type="number" label="Output Alarm"
              step="0.10"
              > </v-text-field>
          </v-col> 

          <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>High Output Alarm</v-col>
          <v-col cols="2" align-self="center" class="text-body-1 font-weight-medium">
            <v-text-field suffix="dBm" dense hide-details
              v-model.number="new_highAlarm"
              type="number" label="Output Alarm"
              step="0.10"
              > </v-text-field>
          </v-col>


        </v-row>

        <v-row>
          <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Low Output Alarm</v-col>
          <v-col cols="2" align-self="center" class="text-body-1 font-weight-medium">
            <v-text-field suffix="dBm" dense hide-details
              v-model.number="new_lowAlarm"
              type="number" label="Output Alarm"
              step="0.10"
              > </v-text-field>
          </v-col>

          <v-col cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>High Output Fault</v-col>
          <v-col cols="2" align-self="center" class="text-body-1 font-weight-medium">
            <v-text-field suffix="dBm" dense hide-details
              v-model.number="new_highFault"
              type="number" label="Output Fault"
              step="0.10"
              > </v-text-field>
          </v-col>
        </v-row>

      </v-container>
      
    </v-card-text>
  </v-card>

</template>


<script>
import { mapActions } from 'vuex';

const PowerConversionOptions = ["dBm", "dBW", "W"];

export default {
  name: 'UnitInfo',

  props: {

    Info: {
      type: Object,
      default: function() {
        return {
          Model: "Model No",
          Serial: "Serial No",
          Firmware: "Firmware Vers",
          Bootloader: "Bootloader Vers"
        }
      }
    },

    Status: {
      type: Object,
      default: function() {
        return {
          TxPower: 38.5,
          SwTxEnabled: true,
          TxRefLocked: true,
          TxStepAtten: 25.5,

          Temperature: 55.4,

          FanSpeed: 3490,
          
          Faults: ['Master', 'Drain Voltage', 'Fan Power'],
        }
      }
    },

    UnitOption: {
      type: Object,
      default: function() {
        return {
          Tx: {
            StepAtten: {
              Min: 0.0,
              Max: 31.75,
              Rez: 0.25
            },

            LoFreq: {
              PresetEnabled: true,
              RangeEnabled: false,
              Presets: [28.05, 29.00]
            },

            Alc: {
              Enabled: false,
              Active: false,
            }
          }
        }
      }
    },
    
    Alias: {
      type: String,
      default: null
    },

    OtherRfSettings: {
      type: Object,
      default: function() {
        return {
          BootupRf: false,
          RfOffset: 3.7,
          PowerConversion: 0
        }
      }
    },

    AlcSettings: {
      type: Object,
      default: function() {
        return {
          SetPoint: 32.1,
          HighAlarm: 30.0,
          HighFault: 40.0,

          LowAlarm: 20.0,
        }
      }
    }
  },

  computed: {
    StepAttenIncrementString() {
      return "Step Increment is " + parseFloat(this.UnitOption.Tx.StepAtten.Rez).toFixed(2) + "dB";
    },
    
    IsDifferent() {
      return this.new_StepAtten != this.Status.TxStepAtten
        || this.new_SwEnable != this.Status.SwTxEnabled
        || this.new_Alias != this.Alias;
    },

  },

  methods: {
    ...mapActions([
      "PerformSetStepAtten", 
      "PerformSetSwTxEnabled", 
      "PerformSetUnitAlias",
      "PerformSetRfOffsetSettings",
      "PerformSetBootupRfSettings",
      "PerformSetRfPowerConversion"
    ]),

    StepAttenIsValid(atten) {
      return atten >= this.UnitOption.Tx.StepAtten.Min
        && atten <= this.UnitOption.Tx.StepAtten.Max
        && atten % this.UnitOption.Tx.StepAtten.Rez == 0;
    },

    IncrementStepAtten(atten) {
      return atten + this.AttenIncrement;
    },


    ConfirmRebootUnit(reboot) {
      if (reboot) {
        console.log("Rebooting Unit");
      }
      this.rebootDialog = false;
    },

    SetNewAlias() {
      if (this.new_Alias != this.Alias) {
        this.PerformSetUnitAlias(this.new_Alias);
      }
    },

  },

  data: function() {
    return {
      new_StepAtten : this.Status.TxStepAtten,
      new_SwEnable: this.Status.SwTxEnabled,
      new_Alias: this.Alias,

      rebootDialog: false,

      new_RfOffset: this.OtherRfSettings.RfOffset,
      new_BootupRf: this.OtherRfSettings.BootupRf,
      new_PowerConversion: this.OtherRfSettings.PowerConversion,

      new_setpoint: this.AlcSettings.SetPoint,
      new_highAlarm: this.AlcSettings.HighAlarm,
      new_highFault: this.AlcSettings.HighFault,
      new_lowAlarm: this.AlcSettings.LowAlarm
    }
  },

  created() {
    this.PowerConversionOptions = PowerConversionOptions;
  },
  watch: {
    new_StepAtten: function() {
      if (this.new_StepAtten != this.StepAtten) {
        this.PerformSetStepAtten(this.new_StepAtten);
      }
    },

    new_RfOffset: function() {
      if (this.new_RfOffset != this.RfOffset) {
        this.PerformSetRfOffsetSettings(this.new_RfOffset);
      }
    },

    new_PowerConversion: function() {
      if (this.new_PowerConversion != this.PowerConversion) {
        this.PerformSetRfPowerConversion(this.new_PowerConversion);
      }
    }
  }

}
</script>

