<template>
  <v-card elevation="2" outlined>
    <v-card-text>
      <p class='text-h6 text--primary'> Status + RF</p>

      <v-container>
        <v-row>
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>RF Mode</v-col>
          <v-col align-self="center" cols="3"
            :class="[IsFaulted? 'red--text' : (Status.SwTxEnabled == false ? 'amber--text' : 'green--text'), 'text-body-1', 'd-flex align-center', 'font-weight-bold']"> 
            <v-tooltip right :disabled="IsFaulted == false">
              <template v-slot:activator="{on}">
                <div v-on="on"> {{IsFaulted ? "Faulted":  (Status.SwTxEnabled == false ? "Muted" : "Active")}} </div>
              </template>
              <span>{{GetFaultsString}}</span>
            </v-tooltip>
            
          </v-col>
        
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Temperature</v-col>
          <v-col align-self="center" cols="3" class='text-body-1 d-flex align-center font-weight-bold'> {{parseFloat(Status.Temperature).toFixed(1)}} &#176;C </v-col>
        </v-row>

        <v-row>
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>RF Power</v-col>
          <v-col align-self="center" cols="3" class="text-body-1 d-flex align-center font-weight-bold">
            {{GetRfPowerLabel}}
          </v-col>
          
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>RF Offset</v-col>
          <v-col align-self="center" cols="3" class='text-body-1 d-flex align-center font-weight-bold'> {{parseFloat(RfOffset).toFixed(2)}} dB</v-col>
        </v-row>

        <v-row>
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Step Attenuator</v-col>
          <v-col align-self="center" cols="3" class='text-body-1 d-flex align-center font-weight-bold'> {{parseFloat(Status.TxStepAtten).toFixed(2)}} dB </v-col>

          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Tx Reference</v-col>
          <v-col align-self="center" cols="3" 
            :class="[Status.TxRefLocked ? 'green--text' : 'red--text', 'text-body-1', 'd-flex', 'align-center', 'font-weight-bold']"> 
            {{Status.TxRefLocked ? "Locked" : "Unlocked"}} 
          </v-col>
        </v-row>

        <v-row>
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Software Mute</v-col>
          <v-col align-self="center" cols="3" 
            :class="[Status.SwTxEnabled ? 'green--text' : 'amber--text', 'text-body-1', 'd-flex', 'align-center', 'font-weight-bold']">
            {{Status.SwTxEnabled ? "Unmuted" : "Muted"}} 
          </v-col>

          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Fan</v-col>
          <v-col align-self="center" cols="3" class='text-body-1 d-flex align-center font-weight-bold'> {{Status.FanSpeed}} RPM </v-col>
        </v-row>

        <v-row>
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>Discrete Mute</v-col>
          <v-col align-self="center" cols="3" 
            :class="[Status.SwTxEnabled ? 'green--text' : 'amber--text', 'text-body-1', 'd-flex', 'align-center', 'font-weight-bold']">
            {{Status.SwTxEnabled ? "Unmuted" : "Muted"}} 
          </v-col>
        </v-row>

        <v-row>
          <v-col align-self="center" cols="2" class='text-body-1 d-flex align-center justify-end font-italic'>ALC Status</v-col>
          <v-col align-self="center" cols="2" :class="[GetAlcStatusColor, 'text-body-1 d-flex align-center font-weight-bold']">
            {{GetAlcStatus}}
          </v-col>
          <v-btn v-on:click="toggleAlcStatus"> Toggle </v-btn>
        </v-row>
        
      </v-container>
      
    </v-card-text>
  </v-card>
</template>


<script>

const AlcStatuses = ["Active", "Inactive", "Warning Output High", "Warning Output Low", "Fault Output High", "Fault Output Low"];
const AlcStatusColors = ["green--text", "", "amber--text", "amber--text", "red--text", "red--text"];

export default {
  name: 'Status',

  props: {

    Status: {
      type: Object,
      default: function() {
        return {
          TxPower: 38.5,
          SwTxEnabled: true,
          TxRefLocked: true,
          TxStepAtten: 25.5,

          Temperature: 55.4,

          FanSpeed: 3490,
          
          Faults: ['Master', 'Drain Voltage', 'Fan Power'],
        }
      }
    },
    
    RfOffset: {
      type: Number,
      default: 3.7
    },

    PowerConversion: {
      type: Number,
      default: 0
    }

  },

  computed: {
    GetFaultsString() {
      if (this.Status.Faults != null && this.Status.Faults.length > 0) {
        var faultMsg = "";
        this.Status.Faults.forEach(fault => {
          if (faultMsg.length == 0) {
            faultMsg = fault;
          }
          else {
            faultMsg = faultMsg + " | " + fault;
          }
        });
        
        return faultMsg;
      }
      return "";
    },

    IsFaulted() {
      return this.Status.Faults != null && this.Status.Faults.length > 0;
    },

    GetComputedPower() {
      return this.Status.TxPower + this.RfOffset;
    },

    GetRfPowerLabel() {
      switch (this.PowerConversion) {
        // dBw
        case 1: 
          return parseFloat(this.GetComputedPower - 30.0).toFixed(1) + " dBW";

        // Watt
        case 2:
          return parseFloat(this.dBmToWatt(this.GetComputedPower)).toFixed(4) + " W";

        // dBm
        default:
          return parseFloat(this.GetComputedPower).toFixed(1) + " dBm";
      }
    },

    GetAlcStatus() {
      return AlcStatuses[this.alcStatus];
    },
    GetAlcStatusColor() {
      return AlcStatusColors[this.alcStatus];
    }
  },

  methods: {
    dBmToWatt(power) {
      return (1 * Math.pow(10, power / 10)) / 1000.0;
    },

    toggleAlcStatus() {
      this.alcStatus = (this.alcStatus + 1) % AlcStatuses.length;
    }
  },

  data() {
    return {
      alcStatus : 0
    }
  }
  
}
</script>

<style scoped>
.text-field{
  border: none !important;
  background: none !important;
  padding: 0px !important;
  color: rgba(0, 0, 0, 0.6) !important;
}
</style>
