<template>
  <v-card elevation="2" outlined>
    <v-card-text>
      <p class='text-h6 text--primary'> Clock (NTP) </p>

      <v-container>
        <v-row>
          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> Manual Time </v-col>
          <v-col cols="4" align-self="center">
            <v-btn-toggle color="primary" mandatory v-model="new_ManualDateTimeEnabled" >
              <v-btn outlined :value="false">
                NTP Server
                <v-icon dark right> mdi-server </v-icon>
              </v-btn>
              <v-btn outlined :value="true"> 
                Manual
                <v-icon dark right> mdi-checkbox-marked-circle </v-icon>
              </v-btn>
            </v-btn-toggle>
          </v-col>
        </v-row>

        <v-row v-if="new_ManualDateTimeEnabled == false">
          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> Primary </v-col>
          <v-col cols="2" align-self="center">
            <v-text-field class="text-body-1 font-weight-medium" dense hide-details
              :rules="[IsValidIp(new_PrimaryAddress)]" v-model="new_PrimaryAddress"
              type="text" label="Primary Address"></v-text-field>
          </v-col>

          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> Secondary </v-col>
          <v-col cols="2" align-self="center">
            <v-text-field class="text-body-1 font-weight-medium" dense hide-details
              :rules="[IsValidIp(new_SecondaryAddress)]" v-model="new_SecondaryAddress"
              type="text" label="Secondary Address"> </v-text-field>
          </v-col>
        </v-row>

        <v-row v-if="new_ManualDateTimeEnabled">
          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic">Entered Time</v-col>
          <v-col cols="4" align-self="center" class="text-body-1 font-weight-medium">
            <input type="datetime-local" class="text-body-1" v-model="new_ManualDateTime" pattern="[0-9]{4}-[0-9]{2}-[0-9]{2}T[0-9]{2}:[0-9]{2}"/> 
          </v-col>
        </v-row>

        <v-row>
          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> UTC Offset </v-col>
          <v-col cols="1" align-self="center">
            <v-text-field class="text-body-1 font-weight-medium" dense hide-details 
              v-model="new_UtcOffsetHour" :rules="[IsValidHourInput(new_UtcOffsetHour)]"
              type="number" label="Hour(s)"
              min="0" max="26">{{parseFloat(UtcOffsetMinutes / 60).toFixed(0)}}</v-text-field>
          </v-col>
          <v-col cols="1" align-self="center">
            <v-text-field class="text-body-1 font-weight-medium" dense hide-details 
              v-model="new_UtcOffsetMinute" :rules="[IsValidMinuteInput(new_UtcOffsetMinute)]"
              type="number" label="Minute(s)"
              min="0" max="59">{{UtcOffsetMinutes % 60}}</v-text-field>
          </v-col>
          
          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> Update Daily </v-col>
          <v-col cols="3" align-self="center">
            <v-btn-toggle color="primary" mandatory v-model="new_UpdateDaily">
              <v-btn outlined :value="true"> 
                Yes
                <v-icon dark right> mdi-checkbox-marked-circle </v-icon>
              </v-btn>
              <v-btn outlined :value="false">
                No 
                <v-icon dark right> mdi-cancel </v-icon>
              </v-btn>
            </v-btn-toggle>
            
          </v-col>
        </v-row>

        <v-row v-if="new_ManualDateTimeEnabled == false">
          <v-col cols="2" align-self="center" class="text-body-1 d-flex justify-end font-italic"> Clock Sync </v-col>
          <v-col cols="2" align-self="center" :class="[IsSynced? 'green--text' : 'red--text', 'text-body-1', 'd-flex' ,'justify-start', 'font-weight-bold']"> 
            {{IsSynced ? "Synced" : "Not Synced"}} 
          </v-col>
        </v-row>

        <v-row>
          <v-col cols="2" class="text-body-1 d-flex justify-end">
            <v-btn class="font-weight-medium"
              :disabled="IsDifferent == false">Submit </v-btn>
          </v-col>
        </v-row>

      </v-container>
      
    </v-card-text>

  </v-card>
</template>

<script>

export default {
  name: 'ClockNtp',

  props: {
    PrimaryAddress: {
      type: String,
      default: "192.168.100.10"
    },
    SecondaryAddress: {
      type: String,
      default: "192.168.200.10"
    },
    ManualTimeEnabled: {
      type: Boolean,
      default: true
    },
    ManualDateTime: {
      type: Number,
      default: 1634781386000
    },
    UpdateDaily: {
      type: Boolean,
      default: false
    },

    UtcOffsetMinutes: {
      type: Number,
      default: 80
    },
    
    IsSynced: {
      type: Boolean,
      default: true
    }
  },

  data: function() {
    return {
      new_PrimaryAddress: this.PrimaryAddress,
      new_SecondaryAddress: this.SecondaryAddress,
      
      new_ManualDateTimeEnabled: this.ManualTimeEnabled,
      new_ManualDateTime: this.IntegerToDateTimeString(this.ManualDateTime),
      
      new_UpdateDaily: this.UpdateDaily,
      
      new_UtcOffsetHour: parseFloat(this.UtcOffsetMinutes / 60).toFixed(0),
      new_UtcOffsetMinute: (this.UtcOffsetMinutes % 60),
    }
  },

  computed: {
    IsDifferent() {
      var isDifferent = false;
      if (this.new_ManualDateTimeEnabled)
      {
        var newTime = new Date(this.new_ManualDateTime);
        var timeInMs = newTime.getTime() - (newTime.getTimezoneOffset() * 60 * 1000);
        isDifferent = timeInMs != this.ManualDateTime;
      }
      else
      {
        isDifferent = this.new_PrimaryAddress != this.PrimaryAddress
          || this.new_SecondaryAddress != this.SecondaryAddress;
      }

      var newOffsetTimeInMinute = (this.new_UtcOffsetHour * 60) + this.new_UtcOffsetMinute;
      return isDifferent 
        || newOffsetTimeInMinute != this.UtcOffsetMinutes
        || this.new_UpdateDaily != this.UpdateDaily
    }
  },

  methods: {
    IsValidIp(addr) {
      var ipformat = /^(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/;
      if (addr.match(ipformat))
      {
        return true;
      }
      return false;
    },

    IsValidDateTime(dateTime) {
      return isNaN(Date.parse(dateTime)) == false;
    },

    IsValidHourInput(hours) {
      var hr = Number.parseInt(hours, 10)
      if (isNaN(hr) == false) {
        return hr >= 0 && hr < 27;
      }
      return false;
    },
    IsValidMinuteInput(minutes) {
      var min = Number.parseInt(minutes, 10)
      if (isNaN(min) == false) {
        return  min >= 0 &&  min < 60;
      }
      return false;
    },

    IntegerToDateTimeString(timeValue) {
      var newDate = new Date(timeValue);
      return newDate.toISOString().split('.')[0];
    }
  },

  watch: {
    
  }
}

</script>